
*{
    box-sizing: border-box;
    
    &[dir='rtl']{
        text-align: right;
    }

    &::-webkit-scrollbar {
        width: 10px;
    }
    &::-webkit-scrollbar-track {
        background: #832;
    }
    &::-webkit-scrollbar-thumb {
        background: #b65;
    }
    &::-webkit-scrollbar-thumb:hover {
        background: #b75;
    }
    &::-webkit-scrollbar-corner {
        background: #832;
    }
}

#main{
    scroll-behavior: smooth;
    min-height: 100vh;
    width: 100%;
    height: 100vh;
    position: absolute;
    overflow:auto;
    top: 0;
    left: 0;
}